<script>
import Search from "../../../src/views/HelpAndGuide/Search.vue";
import DataService from '../../services/DataService';

import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());

import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
//import 'bootstrap/scss/bootstrap.scss'

export default {
    name: 'HelpArticles',

    components: {
        Search,
        VueBootstrapTypeahead,

    },
    data() {
        return {
            HelpArticleList: [],
            ArticleNameList: [],
            Article: '',
            ArticlesContent: '',
            IsPopularSearchShow: true,
            IsDataNotFoundShow: false,
            IsAccordionShow: false,
            isLoading: false,
            Path: Boolean,
            NewValue: Boolean,
            UserType: "",
            PopularSearchesList: [],

        }
    },
    created: function () {

        this.UserType = GetUserInfo.value.UserType;
        this.ArticleList();
        this.GetPopularSearches();

    },
    methods: {
        ArticleList: function () {
            var vm = this;

            vm.isLoading = true;

            var params = `search=&userType=${GetUserInfo.value.UserType}&articleType=HelpArt&moduleId=`

            DataService.GetArticleList(params).then(function (response) {

                if (response.data.length > 0) {

                    response.data.forEach((value, index) => {

                        vm.ArticleNameList.push(value.articleName)
                        vm.isLoading = false;
                    })
                }
               
            });
        },
        SearchData: function () {
            
            var vm = this;
            vm.isLoading = true;
            var params = `search=${vm.Article}&userType=${GetUserInfo.value.UserType}&articleType=HelpArt&moduleId=`;
            DataService.GetArticleList(params).then(function (response) {
                
                if (response.data.length > 0) {
                    vm.HelpArticleList = response.data;
                    vm.IsPopularSearchShow = false;
                    vm.IsAccordionShow = true;
                    vm.isLoading = false;
                } else if (response.data.length == 0) {

                    vm.IsPopularSearchShow = false;
                    vm.IsDataNotFoundShow = true;
                    vm.IsAccordionShow = false;
                    vm.isLoading = false;
                }

            });
        },
        ClearData: function (e) {
            this.Article = '';
            const typeaheadInput = this.$refs.typeahead.$el.querySelector('input');
            if (typeaheadInput) {
                typeaheadInput.value = '';
                typeaheadInput.dispatchEvent(new Event('input'));
            }
            this.IsPopularSearchShow = true;
            this.IsDataNotFoundShow = false;
            this.IsAccordionShow = false;

        },
        GetArticleContent: function (ArticlesId) {
            var vm = this;
            var params = `ArticleId=${ArticlesId}&articleType=HelpArt`;
            DataService.GetArticleDetailById(params).then(function (response) {
                if (response.data != null) {
                    vm.ArticlesContent = response.data.article_content;
                }
            });
        },
        routeArticleById: function (item) {
            
            var vm = this;
            var ArticleId = item.ArticleId;
            var ModuleId = item.ModuleId;
            vm.$router.push({ name: "HelpArticlesPerModule", params: { Id: ArticleId, ModuleId: ModuleId } });
        },

        GetPopularSearches: function () {
            
            var vm = this;

            DataService.GetPopularSearches().then(function (response) {
                
                if (response.data != null) {

                    response.data.Data.forEach((value, index) => {

                        let myobj = {
                            ArticleName: value.ArticleName,
                            ArticleId: value.ArticleId,
                            ModuleId: value.ModuleId
                        }
                        vm.PopularSearchesList.push(myobj);
                    })

                }
            });
        },
    },
};
</script>

